const sentimentColor = (s) => s > 0.1 ? C.green : s < -0.1 ? C.red : C.orange;
const sentimentLabel = (s) => s > 0.1 ? "Positivo" : s < -0.1 ? "Negativo" : "Neutral";

//  COMPONENTS 

function GaugeChart({ value, min = -1, max = 1 }) {
  const pct = ((value - min) / (max - min)) * 180;
  const gaugeColor = value > 0.1 ? C.green : value < -0.1 ? C.red : C.orange;
  return (
    <div style={{ position: "relative", width: 180, height: 100, margin: "0 auto" }}>
      <svg viewBox="0 0 180 100" style={{ width: "100%", height: "100%" }}>
        <path d="M 10 90 A 80 80 0 0 1 170 90" fill="none" stroke={C.border} strokeWidth="12" strokeLinecap="round" />
        <path d="M 10 90 A 80 80 0 0 1 170 90" fill="none" stroke={`url(#gaugeGrad)`} strokeWidth="12" strokeLinecap="round"
          strokeDasharray={`${(pct / 180) * 251.3} 251.3`} />
        <defs>
          <linearGradient id="gaugeGrad" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor={C.green} />
            <stop offset="50%" stopColor={C.orange} />
            <stop offset="100%" stopColor={C.red} />
          </linearGradient>
        </defs>
        <line x1="90" y1="90" x2={90 + 55 * Math.cos((Math.PI * (180 - pct)) / 180)} y2={90 - 55 * Math.sin((Math.PI * (180 - pct)) / 180)}
          stroke="white" strokeWidth="2.5" strokeLinecap="round" />
        <circle cx="90" cy="90" r="5" fill="white" />
      </svg>
      <div style={{ position: "absolute", bottom: 4, left: "50%", transform: "translateX(-50%)", textAlign: "center" }}>
        <span style={{ fontSize: 28, fontWeight: 700, color: gaugeColor, fontFamily: "'JetBrains Mono', monospace" }}>{value.toFixed(2)}</span>
      </div>
    </div>
  );
}

function ProgressBar({ value, max = 100, color = C.cyan, height = 8 }) {
  return (
    <div style={{ width: "100%", height, background: C.border, borderRadius: height / 2, overflow: "hidden" }}>
      <div style={{ width: `${(value / max) * 100}%`, height: "100%", background: `linear-gradient(90deg, ${color}, ${color}88)`, borderRadius: height / 2, transition: "width 0.8s ease" }} />
    </div>
  );
}

function Card({ children, style, onClick, glow, borderColor }) {
  const [hovered, setHovered] = useState(false);
  return (
    <div
      onClick={onClick}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      style={{
        background: C.card,
        borderRadius: 16,
        border: `1px solid ${borderColor || (hovered ? C.borderAccent : C.border)}`,
        padding: 24,
        cursor: onClick ? "pointer" : "default",
        transition: "all 0.3s ease",
        boxShadow: glow ? `0 0 20px ${glow}22, inset 0 1px 0 ${glow}11` : hovered && onClick ? `0 4px 20px ${C.bg}` : "none",
        ...(style || {}),
      }}
    >
      {children}
    </div>
  );
}

function Badge({ children, color = C.cyan, pulse }) {
  return (
    <span style={{
      display: "inline-flex", alignItems: "center", gap: 6,
      padding: "4px 12px", borderRadius: 20, fontSize: 13, fontWeight: 600,
      background: `${color}18`, color, border: `1px solid ${color}33`,
    }}>
      {pulse && <span style={{ width: 8, height: 8, borderRadius: "50%", background: color, animation: "pulse 2s infinite" }} />}
      {children}
    </span>
  );
}

function SWOTCard({ type, items, onItemClick }) {
  const config = {
    strengths: { label: "FORTALEZA", color: C.green, border: `${C.green}55` },
    weaknesses: { label: "DEBILIDAD", color: C.orange, border: `${C.orange}55` },
    opportunities: { label: "OPORTUNIDAD", color: C.cyan, border: `${C.cyan}55` },
    threats: { label: "AMENAZA", color: C.red, border: `${C.red}55` },
  }[type];
  return (
    <div style={{ flex: 1, minWidth: 220 }}>
      <div style={{ fontSize: 11, fontWeight: 700, color: config.color, letterSpacing: 1.5, marginBottom: 12, textTransform: "uppercase" }}>
        {config.label} ({items.length})
      </div>
      {items.slice(0, 3).map((item, i) => (
        <div key={i} onClick={() => onItemClick?.(item)}
          style={{
            padding: 14, marginBottom: 8, borderRadius: 10,
            border: `1px solid ${config.border}`, borderLeft: `3px solid ${config.color}`,
            background: `${config.color}08`, cursor: "pointer",
            opacity: item.confidence >= 0.4 ? 1 : 0.5,
          }}>
          <div style={{ fontSize: 13, color: C.text, lineHeight: 1.4 }}>"{item.text}"</div>
          <div style={{ display: "flex", gap: 8, marginTop: 8, flexWrap: "wrap" }}>
            <span style={{ fontSize: 11, color: C.textMuted }}>Impacto: {item.impact > 0.6 ? "Alto" : item.impact > 0.3 ? "Medio" : "Bajo"}</span>
            <span style={{ fontSize: 11, color: config.color }}> {item.narrative}</span>
            <span style={{ fontSize: 11, color: C.textDim }}>Tema: {item.theme}</span>
          </div>
          {item.source && <div style={{ fontSize: 10, color: C.textDim, marginTop: 4 }}>Fuente: {item.source}</div>}
        </div>
      ))}
    </div>
  );
}

